er.push({\n              path: \"/MunicipalAgent/materiel/materiel/Reject\",\n              query: {\n                id: gid\n              }\n            })\n          })\n        }).catch(err => {\n\n        })\n      }\n    },\n    close () {\n      this.centerDialogVisible = false\n    }\n\n  }\n\n}\n</script>\n<style scoped>\n.main {\n  width: 70%;\n  margin: 93px auto;\n}\n\n@media (max-width: 768px) {\n  .main {\n    border: 1px solid red;\n  }\n}\n\n.information {\n  color: #333333;\n  font-size: 20px;\n}\n\n.information > ul:after {\n  display: block;\n  content: \"\";\n  clear: both;\n}\n\n.information > ul {\n  margin-bottom: 20px;\n}\n\n.information > ul > li {\n  float: left;\n  margin-right: 40px;\n}\n\n.el_table:after {\n  display: block;\n  content: \"\";\n  clear: both;\n}\n\n.el_table {\n  margin-top: 66px;\n}\n\n.el_btn {\n  text-align: center;\n  margin-top: 120px;\n}\n\n.Reason {\n  display: flex;\n  margin-top: 30px;\n}\n\ntextarea {\n  resize: none;\n  width: 80%;\n  height: 150px;\n  margin-left: 10px;\n}\n</style>\n<style>\n.el-table__header {\n}\n\n.a {\n}\n\n@media only screen and (min-width: 1920px) {\n  .a {\n    border: 1px solid red !important;\n  }\n}\n\n.body {\n  width: 70% !important;\n  margin: 0 auto;\n}\n</style>\n"],"sourceRoot":""}]);

